import { Component, OnInit,Input } from '@angular/core';
import { orderStream } from '../delivery-options/services/orderStream';
import { CustomerDataService } from '../../customerModule/moduleServices/customerData';
import { orderGetService } from './services/orderService';

@Component({
  selector: 'admin-current-order-data',
  templateUrl: './order-data.component.html',
  styleUrls: ['./order-data.component.css'],
  providers:[orderGetService]
})
export class OrderDataComponent implements OnInit {
@Input() set Order(value){
this.order=value;
this.standLevel=null;
this.isBtnShow=true;
this.expressLevel=null;
this.isStandardShow=false;
this.isExpressShow=false;
this.initialize();
}
@Input() levels=null;
private order=null;
private isStandardShow=false;
private standLevel=null;
private isExpressShow=false;
private expressLevel=null;
private isBtnShow=true;
private customerName=null;
  constructor(private orderStream:orderStream,private orderService:orderGetService) { }

  initialize(){
    if(this.order.deliverystatus.standardStatus!=null){
      this.standLevel=this.order.deliverystatus.standardStatus;
     
      }
      if(this.order.deliverystatus.expressStatus!=null){
        this.expressLevel=this.order.deliverystatus.expressStatus;
      }
      console.log(this.standLevel,this.expressLevel);
    this.orderService.getCustomerData({id:this.order.customerId}).subscribe(data=>{
      this.customerName=data.customer.firstName;
    })
  }

  getMoreData(){
    if(this.standLevel!=null){
      this.orderService.getStandardDeliveryData({id:this.standLevel}).subscribe(data=>{
        if(data.status=='S'){
          this.order.deliverystatus.standardStatus=data.STdelivery;
          this.isStandardShow=true;
          this.isBtnShow=false;
        }else{
          alert('Error Occured');
        }
      },err=>{
        if(err.status!=null){
        alert(err.message);
      }else{
        alert(err);
      }
      })
    }

    if(this.expressLevel!=null){
      this.orderService.getExpressDeliveryData({id:this.expressLevel}).subscribe(data=>{
        if(data.status=='S'){
          this.order.deliverystatus.expressStatus=data.EXPdelivery;
          this.isExpressShow=true;
          this.isBtnShow=false;
        }else{
          alert('Error Occured');
        }
      },err=>{
        if(err.status!=null){
        alert(err.message);
      }else{
        alert(err);
      }
      })
    }
    
  }


  ngOnInit() {
this.initialize();
  }
changeLevel(statusType,delobj){
let levelobj={deliveryId:delobj.deliveryId,delType:statusType,levelValue:delobj.maxLevel,placingdate:this.order.placingdate};
console.log(levelobj);
this.orderService.changeLevel(levelobj).subscribe(data=>{
  if(data.status=='S' && data.isPushed==true){
window.location.reload();
  }else{
    alert('Error Occured');
  }
},err=>{
  if(err.status!=null){
  alert(err.message);
}else{
  alert(err);
}
})
}
}
