import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common'
import { Routes, RouterModule } from '@angular/router';
import {productUploadComponent} from '../productUpload/productUpload.component'
import {categoryCrud} from '../categoryCrud/categoryCrud'
import {AdspageComponent} from '../adspage/adspage.component'
import { DashboardComponent } from './dashboard.component';
import { MakerCheckerComponent } from '../maker-checker/maker-checker.component';
import {FooterCrudComponent } from '../footer-crud/footer-crud.component';
import { DelivOptPageComponent } from '../deliv-opt-page/deliv-opt-page.component';
import { OrderOptionsComponent } from '../delivery-options/order-options.component';
import { VoucherComponent } from '../voucher/voucher.component';
import { AddressCrud } from '../AddressCrud/address.component';


const routes: Routes = [
  {path:'', component:DashboardComponent,
   children:[
      {path:'categorycrud',component:categoryCrud},
      {path:'productUpload',component:productUploadComponent},
      {path:'makerChecker',component:MakerCheckerComponent},
      {path:'adsCrud',component:AdspageComponent},
      {path:'abtCrud',component:FooterCrudComponent},
      {path:'delivOptLevel',component:DelivOptPageComponent},
      {path:'currentOrders',component:OrderOptionsComponent},
      {path:'voucherCrud' ,component:VoucherComponent},
      {path:'addressCrud',component:AddressCrud}
     ]}
    
  ];


@NgModule({
    imports: [CommonModule,RouterModule.forChild(routes)],
    exports: [RouterModule]
  })

  export class DashboardRoutingModule { }
  
  